.app{
    widht: 100%;
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
   background-color: antiquewhite;

} 

h1{
    font-size: 50px;
}

span{
    margin: 12px 0;
}

.area-input{
    widht: 500px;
    ddisplay: flex;
    flex-direction: column;
    align-items: center;
}

.area-input input{
    width: 100%;
    margin-bottom:  12px;
    height: 46px;
    background-color: #121212;
    border-radius: 6px;
    border: 0;
    color: #fff;
    padding: 0 12px;
    text-align: center;
    font-size: 18px;
}

.area-input button{
    width: 100%;
    height: 46px;
    border: 0;
    border-radius: 6px;
    background-color: #357aff;
    color: #fff;
    font-size: 18px;
}

h2{
    margin-top: 28px;

}
